* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

.flag {
  width: 30px;
}
.flag--detail {
  width: 50px;
}
.country {
  display: flex;
  align-items: center;
}
.country--detail {
  margin-bottom: 20px;
}
.country__name {
  font-size: 20px;
  font-weight: 500;
  margin: 0 0 0 10px;
}
.country__name--detail {
  font-size: 30px;
  font-weight: 700;
}
.details {
  padding: 0;
  margin-top: 30px;
}
.details__item {
  display: flex;
  align-items: center;
  font-weight: 700;
  margin-bottom: 10px;
}
.details__text {
  margin: 0 0 0 10px;
  font-weight: 400;
}
